            <div class="thankyou-thumbnail">
              <a class="thankyou-thumbnail-link" href="#/workouts?filter=cardio&trainer=ariel">
                <img class="thankyou-thumbnail-image" src="@/assets/img/thankyou/thankyou-ariel.v1.png" />
                <div class="thankyou-thumbnail-textbox">
                  <div class="thankyou-thumbnail-copy">Knock out your cardio in 10 minutes</div>
                  <div class="thankyou-thumbnail-train">Train With Ariel ></div>
                </div>
              </a>
            </div>

            <div class="thankyou-thumbnail">
              <a class="thankyou-thumbnail-link" href="#/workouts?filter=conditioning&trainer=steven">
                <img class="thankyou-thumbnail-image" src="@/assets/img/thankyou/thankyou-steven.v1.png" />
                <div class="thankyou-thumbnail-textbox">
                  <div class="thankyou-thumbnail-copy">Rev your metabolism with these 5 exercises</div>
                  <div class="thankyou-thumbnail-train">Train With Steven ></div>
                </div>
              </a>
            </div>

            <div class="thankyou-thumbnail">
              <a class="thankyou-thumbnail-link" href="#/workouts?filter=mobility&trainer=mike">
                <img class="thankyou-thumbnail-image" src="@/assets/img/thankyou/thankyou-mike.v1.png" />
                <div class="thankyou-thumbnail-textbox">
                  <div class="thankyou-thumbnail-copy">Build a more mobile and flexible body</div>
                  <div class="thankyou-thumbnail-train">Train With Mike ></div>
                </div>
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  import DfpAdComponent from '@/components/shared/DfpAd.vue';
  export default {
    name: 'ThankYou',
    components: { DfpAdComponent }
  };
</script>


<style lang="scss" scoped>
  @import '~bulma/sass/utilities/_all';
  @import '~bulma/sass/grid/_all';
  @import '../../assets/sass/colors.scss';

  #amex-adunit {

    margin-top: 50px;

    .google_image_div {
      position: relative;
      overflow: visible;
    }

  }

  .heading-text {
      font-size: 16px;
      letter-spacing: 1px;
      text-transform: uppercase;
      color: $darkGray;
      margin-bottom: 15px;
  }

   .tat-timing {
        .txt-underline {
            border-bottom: 2px solid;
        }
        .tat-heading {

            font-weight: bold;
        }
        ul {
            margin-top: 10px;
            list-style: none;
            font-family: 'BrownStd-Light', Helvetica, Arial, sans-serif;
            font-size: 12px;
            .days {
                font-weight: bold;
                font-family: 'BrownStd-Regular', Helvetica, Arial, sans-serif;
            }
        }
        @media screen and (min-width: $tablet + 1) {

            ul {
                font-size: 18px;
            }
        }
    }
  .sub-heading-text {

      font-family: 'BrownStd-Light', Helvetica, Arial, sans-serif;
      padding: 1rem 1.25rem;
      line-height: 1;
      font-size: 14px;
      margin-bottom: 20px;
      margin-top: -20px;
  }

  .thankyou-line {
    border-bottom: 1px solid $medGray;
    width: 80%;
    margin: auto;
    margin-top: 45px;
    margin-bottom: -45px;
  }

  .thankyou-thumbnail-train{  ///
    margin-top: 15px;
  }


  .thankyou-thumbnail-link {
    color: #4a4a4a;
  }

  .thankyou-thumbnail-container {
    align-items: left;
    display: block;

    .thankyou-thumbnail {

      height: 150px;
      align-items: center;

      .thankyou-thumbnail-link {
        color: #4a4a4a;
      }

      .thankyou-thumbnail-image {
        display: inline;
        // width: 40%;
        // float: left;
        padding-right: 20px;
        min-width: 250px;
      }

      .thankyou-thumbnail-textbox {
        display: inline;
        // width: 50%;
        // height: 200px;
        text-align: left;


        .thankyou-thumbnail-copy {
          font-size: 16px;
          padding-top: 2%;
        }

        .thankyou-thumbnail-train {
          font-size: 12px;
        }

      }

    }

  }

  @media screen and (min-width: $tablet) {

    .heading-text {
        font-size: 24px;
    }
    .sub-heading-text {
        font-size: 18px;
    }

    .thankyou-thumbnail-container {
      display: flex;
      justify-content: center;
      margin: auto;
      margin-bottom: 100px;

      .thankyou-thumbnail {
        display: inline;
        align-items: center;
        padding-right: 20px;
        clear: both;

        .thankyou-thumbnail-image {
          width: 100%;
          min-width: 200px;
          margin-bottom: 5px;
        }

        .thankyou-thumbnail-textbox {
          display: inline;
          float:none;
          text-align: center;

        }

      }

    }

  }

  @media screen and (max-width: $tablet + 1){
    .section {
      padding: 3em 0;
      .thankyou-thumbnail-container {

        clear: both;
        .thankyou-thumbnail {

          padding-right: 0px;
          clear: both;


          .thankyou-thumbnail-image {
              min-width: unset;
              margin-bottom: 5px;
              padding-right: 0px;
              width: 50%;
              float: left;
          }

          .thankyou-thumbnail-textbox {
            width: 50%;
            padding: 8px 10px 0 18px;
            display: block;
            float: left;
            text-align: left;
            font-size: 12px;
            line-height: 18px;

          }

        }

      }
    }
  }

</style>



