import About from '@/views/pages/About';
import Classes from '@/views/pages/Classes';
import Coaches from '@/views/pages/Coaches';
import Workouts from '@/views/pages/Workouts';
import TextTrainer from '@/views/pages/TextTrainer';
import ThankYou from '@/views/pages/ThankYou';
import TermCondition from '@/views/pages/TermCondition';
import Vue from 'vue';
import Router from 'vue-router';

/*
|-------------------------------------------------------------------------------
| routes.js
|-------------------------------------------------------------------------------
| Contains all of the routes for the application
*/

/*
    Imports Vue and VueRouter to extend with the routes.
*/
/*
    Extends Vue to use Vue Router
*/
Vue.use(Router);

/*
    Makes a new VueRouter that we will use to run all of the routes
    for the app.
*/
export default new Router({
  routes: [
    {
      path: '/',
      name: 'about',
      component: About
    },
    {
      path: '/workouts',
      name: 'workouts',
      component: Workouts
    },
    {
      path: '/classes',
      name: 'classes',
      component: Classes
    },
    {
      path: '/coaches',
      name: 'coaches',
      component: Coaches
    },
    {
      path: '/textatrainer',
      name: 'textatrainer',
      component: TextTrainer
    },
    {
      path: '/thankyou',
      name: 'thankyou',
      component: ThankYou
    },
    {
      path: '/termcondition',
      name: 'termcondition',
      component: TermCondition
    }
  ],
  scrollBehavior(to, from, savedPosition) {
    if (to.hash) {
      return {
        selector: to.hash
        // , offset: { x: 0, y: 10 }
      }
    }
    return { x: 0, y: 0 };
  }
});



