<template>
  <div class="columns" @click="loadVideo()" v-bind:id="category + '-' + instructor">
    <div class="video-thumbnail column is-half">
      <a class="thumbnail-link" v-bind:data-available-date="isAvailableOn">
        <img v-bind:src="require(`@/assets/img/thumbnails/${category}/${instructor}_${category}.png`)" />
      </a>
    </div>
    <div class="video-info column is-half">
      <div class="video-title is-uppercase">
        <a class="title-link">
          <b v-html="title"></b>
        </a>
      </div>
      <div v-if="playing" class="is-playing">Playing now</div>
    </div>
  </div>
</template>

<script>
import { EventBus } from '../../../event-bus.js';
import store from '../../../store/store.js';

export default {
  name: 'ThumbnailComponent',
  store: store,
  props: ['url', 'title', 'description', 'category', 'instructor', 'playing','isAvailableOn'],
  data() {
    return {
      activeButton: 'view_all',
      myFilter: ''
    };
  },
  methods: {
    loadVideo: function() {
      if(!this.isAvailableOn) {
        var obj = {
          url: this.url,
          title: this.title,
          description: this.description,
          id: this.id
        };
        EventBus.$emit('thumbnail-clicked', obj);
      }
      
    }
  }
};
</script>

<style lang="scss" scoped>
@import '~bulma/sass/utilities/_all';
@import '../../../assets/sass/colors.scss';

.columns {
  @media screen and (max-width: $tablet) {
    display: flex;
    margin-top: 0;
  }
  .video-thumbnail {
    .thumbnail-link {
      position: relative;
      &:before {
        background-color: #000;
        content:"";
        left: 0;
        height: 100%;
        opacity: 0;
        position: absolute;
        top: 0;
        transition: opacity 0.5s;
        width: 100%;
      }
      &:hover {
        display: block;
        &:after {
          content: attr(data-available-date);
          left: 35%;
          position: absolute;
          top: 35%;
          color:white;
         
        }
        &:before {
          opacity: 0.25;
        }
        
      }
    }
  }
  .video-info {
    .video-title {
      .title-link {
        color: $darkGray;
      }
    }
  }
}
</style>



