<template>
  <div class="text-trainer">

    <div class="section container" id="classlist-section">
      <div class="columns classlist">
        <div class="column has-text-centered">
            <div class="heading-text">Want expert advice on Platform X workouts from an Equinox trainer?</div>

            <div class="sub-heading-text">Get real-time training tips from Platform X backed by the Platinum Card<sup>&#174;</sup> from American&nbsp;Express.</div>
            <a class="button is-uppercase is-grey btn-txt-trainer" @click="scrollToForm()" >SIGN UP FOR TEXT-A-TRAINER</a>
        </div>
      </div>
    </div>
    <div class="section container demo-trainer">
      <div class="columns is-mobile classlist">
        <div class="column is-one-third-mobile is-one-third-tablet is-half-desktop has-text-centered">
            <img src="@/assets/img/mobile-shape.png" class="is-hidden-desktop is-hidden-tablet"/>
            <img src="@/assets/img/desktop-mobile.png" class="is-hidden-mobile desktop-image" />
        </div>
        <div class="column text-steps">
          <ol>
            <li><span>Enter your mobile number to get started.</span></li>
            <li><span>Your welcome text with instructions will arrive.</span></li>
            <li><span>Reply back with your questions on Platform X workouts and receive expert advice from an Equinox trainer within 30 minutes.</span></li>
          </ol>
          <div class="tat-timing">
              <span class="txt-underline"><span class="tat-heading">Hours of operation </span>(EST) </span>
              <ul>
                  <li><span class="days">Monday-Friday:</span> 6AM-12PM & 6PM-12AM</li>
                  <li><span class="days">Saturday:</span> 8AM-2PM</li>
                  <li><span class="days">Sunday:</span> CLOSED</li>
              </ul>
          </div>
        </div>
      </div>
    </div>

    <div class="section container tat-form" id="tat-form">
        <div class="columns classlist">
            <div class="column has-text-centered">

                <div>GET STARTED WITH TEXT-A-TRAINER</div>
                <div id="form-error" class="error error-common">**These fields are required.**</div>
                <form id="smooch-form" class="form">
                    <div>
                        <input class="input tat-fname" type="text" id="fname" placeholder="* FIRST NAME" >
                        <span id="error-fname" class="error">(Name is required.)</span>
                    </div>
                    <div>
                        <input class="input tat-number" type="text" id="number" placeholder="* Phone # (E.g.: 1234567890)">
                        <span id="error-phone" class="error">(Phone # is required.)</span>
                        <span id="error-phone-valid" class="error">(Only numbers are allowed. E.g. 1234567890)</span>
                    </div>

                    <div class="field">
                        <div class="control">
                            <label class="checkbox">
                                <input type="checkbox" id="optin-terms">

                                <span>* By checking this box and clicking "sign up" below, I agree to receive autodialed text messages from or on behalf of Equinox and its trainers/staff to the phone number I provided in connection with the Text-A-Trainer program (the "Program") and I agree to the <a href="#/termcondition" target="_blank" class="tat-tnc-link">Program Terms</a>. Standard text message and data rates apply. Enrolling in the Program is not a condition of any purchase. Program ends 10/28/2018.<span id="error-optin-terms" class="error">(Must agree to terms.)</span></span>
                            </label>
                            <!-- <span id="error-optin-terms" class="error">Please agree to T&C</span> -->
                        </div>
                    </div>
                    <div class="field" >
                        <div class="control">
                            <label class="checkbox">
                                <input type="checkbox" id="optin-newsletter" v-model="checkedNewsletter" @click="clearEmailField()">
                                <span>I also want to receive Furthermore's weekly workout curated by Equinox experts.(<a href="https://furthermore.equinox.com/privacy" target="_blank" class="tat-tnc-link">Policy</a>)</span>

                            </label>
                            <span id="error-optin-newsletter" class="error">Please agree for newsletter </span>
                        </div>
                    </div>
                   <div class="field-email">
                        <input class="input tat-email" type="text" placeholder="EMAIL" id="email" v-bind:disabled="!checkedNewsletter">
                        <span id="error-email" class="error">(Email is required for Furthermores weekly workouts.)</span>
                        <span id="error-email-valid" class="error">(Please enter a valid email address.)</span>
                    </div>
                    <a @click="submitForm($event)"  id="btn_trainer" class="button is-uppercase is-grey btn-txt-trainer">SIGN UP FOR TEXT-A-TRAINER <span class="loader"></span></a>
                </form>
            </div>
            <div id="smooch-chat"></div>
        </div>
    </div>


  </div>
</template>

<script>
import ButtonComponent from '@/components/shared/Button.vue';
import axios from 'axios';

export default {
  name: 'TextTrainer',
  components: { ButtonComponent },
  data() {
    return {
      checkedNewsletter: false
    };
  },
   methods: {

        clearEmailField: function() {

          var newsLetterCheckBox = document.getElementById('optin-newsletter');
            if (newsLetterCheckBox.checked === false ) {

              var emailInput = document.getElementById('email');
              emailInput.value = "";
          };

        },

        scrollToForm: function() {
            var formElem = document.getElementById('smooch-form');
            formElem.scrollIntoView({behavior: "smooth"});
        },
        submitForm: function(e) {
            document.getElementsByClassName('loader')[0].style.display = 'inline';
            document.getElementById('btn_trainer').setAttribute('disabled','disabled');
            var routerPush = this.$router,
                txt_number = document.getElementById('number'),
                number = txt_number.value,
                txt_fname = document.getElementById('fname'),
                fname = txt_fname.value,
                txt_email = document.getElementById('email'),
                email = txt_email.value,
                chk_optinTerms = document.getElementById('optin-terms'),
                optinTerms = chk_optinTerms.checked,
                chk_optinNewsletter = document.getElementById('optin-newsletter'),
                optinNewsletter = chk_optinNewsletter.checked;
            var isValidate = true;
            if(fname === '') {
                txt_fname.classList.add('error-input');
                document.getElementById('error-fname').style.display = 'block';
                isValidate = false;
            } else {
                txt_fname.classList.remove('error-input');
               document.getElementById('error-fname').style.display = 'none';
            }

            if(number === '' ) {
                txt_number.classList.add('error-input');
                document.getElementById('error-phone').style.display = 'block';
                isValidate = false;
            } else {
                document.getElementById('error-phone').style.display = 'none';
                var numberValidate = isNaN(number);
                if(number.indexOf('+') === 0) {
                    numberValidate = isNaN(number.substr(1));
                }
                if(numberValidate) {
                    txt_number.classList.add('error-input');
                    document.getElementById('error-phone-valid').style.display = 'block';
                    isValidate = false;
                } else {
                    document.getElementById('error-phone-valid').style.display = 'none';
                    txt_number.classList.remove('error-input');

                }
                if (!number.match(/^\+/)) { number = "+1" + number }

            }
            if(optinNewsletter) {
                if(email !== '') {
                    document.getElementById('error-email').style.display = 'none';
                    if(!(/^((([a-z]|\d|[!#\$%&'\*\+\-\/=\?\^_`{\|}~]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])+(\.([a-z]|\d|[!#\$%&'\*\+\-\/=\?\^_`{\|}~]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])+)*)|((\x22)((((\x20|\x09)*(\x0d\x0a))?(\x20|\x09)+)?(([\x01-\x08\x0b\x0c\x0e-\x1f\x7f]|\x21|[\x23-\x5b]|[\x5d-\x7e]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(\\([\x01-\x09\x0b\x0c\x0d-\x7f]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]))))*(((\x20|\x09)*(\x0d\x0a))?(\x20|\x09)+)?(\x22)))@((([a-z]|\d|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(([a-z]|\d|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])*([a-z]|\d|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])))\.)+(([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])*([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])))$/i).test(email)) {
                        document.getElementById('error-email-valid').style.display = 'block';
                        txt_email.classList.add('error-input');
                        isValidate = false;
                    } else {
                        document.getElementById('error-email-valid').style.display = 'none';
                        txt_email.classList.remove('error-input');
                    }

                } else  {
                    document.getElementById('error-email').style.display = 'block';
                    isValidate = false;
                }
            }

            if(!optinTerms) {
                document.getElementById('error-optin-terms').style.display = 'inline';
                chk_optinTerms.classList.add('error-input-chk');
                isValidate = false;
            } else {
                document.getElementById('error-optin-terms').style.display = 'none';
                chk_optinTerms.classList.remove('error-input-chk');
            }

            e.preventDefault();
            //{"Email":"rachit.arora@3pillarglobal.com","OptIns":[{"OptIn":true,"OptInCode":"WORKOUTEMAIL","AdditionalNewsletterOptIn":"true"}],"SendMe":true}

            if(isValidate) {
                document.getElementById('form-error').style.display = 'none';
                if(email !== '') {
                    axios.post(`https://furthermore.equinox.com/newsletter/subscribe`, {
                        Email: email,
                        SendMe: true,
                        OptIns: [{"OptIn":true,"OptInCode":"WORKOUT","AdditionalNewsletterOptIn":"true"}]
                    })
                    .then(response => {
                        (response);
                    })
                    .catch(e => {
                        this.errors.push(e)
                    })
                }
                Smooch.init({ appId: '5b744cd5b48af00021bf270e', embedded: true }).then(function() {

                    Smooch.sendMessage({
                        type: 'text',
                        text: 'Hi my name is ' + fname + ' and Im interested in the Text-A-Trainer program.',
                        name: fname,
                        metadata: {
                            "sms": number
                        }
                    }).then(function(){
                        document.getElementsByClassName('loader')[0].style.display = 'none';
                        document.getElementById('btn_trainer').removeAttribute('disabled')
                        routerPush.push({ path: 'thankyou' });
                    });
                    Smooch.updateUser({
                        givenName: fname

                    });
                });
                Smooch.render(document.getElementById('smooch-chat'));
            } else {
                document.getElementsByClassName('loader')[0].style.display = 'none';
                document.getElementById('form-error').style.display = 'block';

                document.getElementById('btn_trainer').removeAttribute('disabled')
            }

        }
  }
};


</script>

<style lang="scss" scoped>
@import '~bulma/sass/utilities/_all';
@import '~bulma/sass/grid/_all';
@import '../../assets/sass/colors.scss';
@import '../../assets/sass/button.scss';

.text-trainer {

  #error-fname, #error-phone, #error-email, #error-phone-valid, #error-email-valid {
      color: #AC0317;
  }

  .field-email {
    margin-left: 20px;
  }

  #tat-form {
    width: 80%;
    margin: auto;

  }

  #email {
    margin-top: -27px;
  }

  #smooch-chat {
     display: none;
   }

   @media screen and (max-width: 1088px) {

     .desktop-image {
       display: none;

     }

     .is-hidden-desktop {
       &.is-hidden-tablet {
         display: inline !important;
       }
     }

     .text-steps {
       margin-right: 5% !important;
     }


   }

   @media screen and (max-width: 768px) {

     .is-hidden-desktop {
       &.is-hidden-tablet {
         width: 60%;
       }
     }

   }

   @media screen and (max-width: 700px) {

     .is-hidden-desktop {
       &.is-hidden-tablet {
         width: 100%;
       }
     }

   }


    .desktop-image {
        width: 40%;
        max-width: 60%;

        @media screen and (max-width: 1020px) {
          width: 60%;
        }

    }
    .tat-form {
        .columns.classlist {
            .tat-tnc-link {
                text-decoration: underline;
                color:#4a4a4a;
                &:hover {
                    font-weight: bold;
                }
            }
            @media screen and (min-width: $tablet + 1) {
                margin: 0 50px;
            }
        }
        .error ,
        #error-email,
        #error-email-valid,
        #error-optin-terms{
                display: none;
                text-align: left;
                padding-top: 5px;
                font-size: 0.75rem;
                font-weight: 400;

                font-style: italic;
        }
        .error-common {
            text-align: center;
            color: #AC0317;
        }
        #error-optin-terms {
            display: inline;
            padding-left: 0px;
        }
        .form {
            .loader {
                display: none;
            }
            div {
                margin-bottom: 20px;
            }

            .error-input {
                border-bottom: 1px solid #AC0317 !important;
            }
            .error-input-chk {
               outline: 1px solid #AC0317 !important;
               & + span {
                   color: #AC0317;
               }
            }
            .checkbox {
                font-family: 'BrownStd-Light', Helvetica, Arial, sans-serif;
                input[type="checkbox"] {
                    position: absolute;
                }
                span {
                    display: block;
                    padding-left: 20px;
                    font-size: 0.75rem;
                }
            }
            input {
                border: none;
                box-shadow: unset;
                background-color: transparent;
                border-bottom: 1px solid #000;
                border-width: 0 0 1px;
                padding: 10px 0 0;
                font-size: medium;
                font-size: 1.1875rem;
                text-transform: uppercase;
                transition: border-color 1s;
                border-radius:unset;
                font-family: 'BrownStd-Light', Helvetica, Arial, sans-serif;
            }
        }
    }
    .tat-timing {
        .txt-underline {
            border-bottom: 2px solid;
        }
        .tat-heading {

            font-size: 18px;

            font-weight: bold;
        }
        ul {
            margin-top: 10px;
            list-style: none;
            font-family: 'BrownStd-Light', Helvetica, Arial, sans-serif;
            font-size: 12px;
            .days {
                font-weight: bold;
                font-family: 'BrownStd-Regular', Helvetica, Arial, sans-serif;
            }
        }
        @media screen and (min-width: $tablet + 1) {

            ul {
                font-size: 18px;
            }
        }
    }
    .demo-trainer {
        background: $lightGray;
        .text-steps {
            font-family: 'BrownStd-Light', Helvetica, Arial, sans-serif;
            font-size: 14px;
            margin-right: 15%;
            margin-left: -30px;

            @media screen and (max-width: 1088px) {
              margin-left: 40px;
            }


            ol {

                li {
                  counter-increment: list;
                  font-size: 36px;
                  line-height: 1;

                  &:not(:first-child) {

                    padding-top: 30px;

                  }

                    span {
                      font-size: 24px;
                      line-height: .6;
                      position: relative;
                      right: -10px;

                    }
                }
            }

            .tat-timing {
              font-size: 24px;
              margin-top: 20px;

              .tat-heading {

                font-size: 24px;

              }
            }

            @media screen and (max-width: 767px) {

              margin-right: 2%;
              font-size: 14px;
              margin-left: 20px;

              ol {

                  li {
                    font-size: 14px;
                    line-height: 1.5;
                    margin: 0 0 15px 0;
                    font-weight: bold;

                    &:not(:first-child) {

                      padding-top: 3px;

                    }

                      span {
                        font-size: 14px;
                        font-weight: normal;
                        position: relative;
                        right: -10px;

                      }

                  }


              }

              .tat-timing {
                font-size: 14px;
                margin-left: 10px;

                .tat-heading {
                  font-size: 14px;
                }
              }

            }

        }

        @media screen and (max-width: 1020px) {

          .text-steps {
            margin-left: 40px;
            margin-right: 5%;
          }


          .desktop-image {



          }
        }
    }
    #classlist-section {
        background: #dde0e3;
        .margin-top-20 {
            margin-top:  20px;
        }
        .columns {
            justify-content: space-between;
            padding: 0 0.75rem;
            .heading-text {
                letter-spacing: 2px;


            }
            .sub-heading-text {
                font-family: 'BrownStd-Light', Helvetica, Arial, sans-serif;
                padding: 1rem 1.25rem;
                line-height: 1;
                font-size: 14px;
            }
        .column.is-three-fifths-desktop {
            border-right: 1px solid $medGray;
            @media screen and (max-width: $desktop) {
            border-right: none;
            }
            img {
            @media screen and (max-width: $tablet) {
                padding-bottom: 30px;
                max-width: 500px;
                width: 70vw;
            }
            }
        }
        @media screen and (min-width: $tablet + 1) {
            .heading-text {
                font-size: 24px;
                font-weight: bold;
            }
            .sub-heading-text {
                font-size: 18px;
            }
        }
        }
    }
    .section,.container {
        width: 100%;
        max-width: 100%;
        margin: 0 5px;
    }
    .project-experience-callout {
        padding: 40px 0;
        margin-bottom: 0;
        line-height: 0.5;
        text-align: center;
        position: relative;
        span {
        display: inline-block;
        position: relative;
        &::before,
        &::after {
            content: '';
            position: absolute;
            height: 2px;
            border-bottom: 1px solid $darkGray;
            top: 5px;
            width: 210px;
            @media screen and (max-width: $desktop) {
            width: 12vw;
            }
        }
        &::before {
            right: 100%;
            margin-right: 15px;
        }
        &::after {
            left: 100%;
            margin-left: 15px;
        }
        }
    }
}
</style>



