<template>
  <div v-if="location === 'internal'">
    <slot></slot>
  </div>
  <div v-else-if="filter">
    <a @click="filterVideos(filter, $event, false)" v-bind:id="id" v-bind:class="['button', 'is-uppercase', type]">
      <slot></slot>
    </a>
  </div>
  <div v-else>
    <a v-bind:href="link" target="_blank" v-bind:class="['button', 'is-uppercase', type]">
      <slot></slot>
    </a>
  </div>
</template>

<script>
import { EventBus } from '../../event-bus.js';
import store from '../../store/store.js';

export default {
  name: 'ButtonComponent',
  store: store,
  props: ['link', 'type', 'location', 'filter', 'id'],
  data() {
    return {
      activeButton: 'view_all',
      myFilter: ''
    };
  },
  methods: {
    filterVideos: function(videoFilter, event, btnClick) {
      this.toggleButton(event);
      var store = this.$store;
      var defTrainer = this.$store.url ? this.$route.query.trainer : undefined
      
      var videos;
     
      if (videoFilter === 'view_all') {
        videos = store.state.workouts_page.videos;
        EventBus.$emit('filter-clicked', videoFilter);
        var obj = {
          url: store.state.workouts_page.videos[0].url,
          title: store.state.workouts_page.videos[0].title,
          description: store.state.workouts_page.videos[0].description
        };
        EventBus.$emit('thumbnail-clicked', obj);
      } else {
        EventBus.$emit('filter-clicked', videoFilter);
        videos = store.state.workouts_page.videos.filter(function(s) {
          var obj;
          var allVideo = store.state.workouts_page.videos;
          defTrainer = defTrainer || 'ariel'; 
          if (s.category.indexOf(videoFilter) > -1 && s.coach === defTrainer) {
              obj = {
                url: s.url,
                title: s.title,
                description: s.description,
                icon: false
              }
              EventBus.$emit('thumbnail-clicked', obj);
          }
          return s.category.indexOf(videoFilter) > -1;
        });
      }
      var queryObj = { filter: videoFilter};
      if(this.$store.url) {
        queryObj = { filter: videoFilter, trainer: defTrainer};
        this.$store.url = false;
      }
      this.$router.push({ query: queryObj});
    },
    toggleButton: function(event) {
      var currentActive = document.querySelector('.button.is-active');
      if (currentActive) currentActive.classList.remove('is-active');

      var el = event.target;
      el.classList.add('is-active');
    }
  }
};
</script>

<style lang="scss" scoped>
@import '~bulma/sass/utilities/_all';
@import '../../assets/sass/colors.scss';
@import '../../assets/sass/button.scss';


</style>



