<template>
  <div id="app-layout">
    <navigation-component></navigation-component>
    <div class="main-content" v-bind:class="{'is-workout':(this.$route.path === '/workouts')}">
      <router-view></router-view>
      <router-link v-if="this.$route.path !== '/textatrainer'" to="/textatrainer" tag="a" class="btn-tat"><img src="@/assets/img/tat_btn.png"/></router-link>
    </div>
    
  </div>
</template>

<script>
import NavigationComponent from '../components/navigation/Navigation.vue';

export default {
  name: 'Layout',
  components: { NavigationComponent }
};
</script>

<style lang="scss" scoped>
@import '~bulma/sass/utilities/_all';

.main-content {
  margin-top: 124px;
   .btn-tat {
    position: fixed;
    bottom: 20px;
    right: -90px;
    z-index: 3;
    img {
     width: 50%; 
    }
     @media screen and (max-width: $tablet) {
      width: 100px ;
      right: 0px;
      img {
        width: 80%;
      }
    }
  }
  @media screen and (max-width: $tablet) {
      margin-top: 100px ;
    }
  &.is-workout {
    @media screen and (max-width: $tablet) {
      margin-top: 267px ;
    }
    
    @media screen and (max-width: 450px) {
      margin-top: 200px ;
    }
  }
 
 
  @media screen and (max-width: $desktop) {
  }
}
</style>



