<template>
  <div class="main">
    <hero-component
      v-for="(hero) in $store.state.about_page.heroes"
      v-bind:text="hero.text"
      v-bind:image="hero.image"
      v-bind:buttonText="hero.buttonText"
      v-bind:buttonLink="hero.buttonLink"
      v-bind:calloutImage="hero.calloutImage"
      v-bind:bottomImage="hero.bottomImage"
      v-bind:topImage="hero.topImage"
      v-bind:topTextImage="hero.topTextImage"
      
      :key="hero.id">
    </hero-component>
    
    <callout-component>
      <div class="section launch-banner">
        <div class="container">
          <div class="card">
            <a href="#/textatrainer">
              <img src="@/assets/img/tat--launch-mobile.png" class="is-hidden-desktop is-hidden-tablet"/>
              <img src="@/assets/img/tat--launch-desktop.png" class="is-hidden-mobile desktop-image" />
            </a>
          </div>
        </div>
      </div>
    </callout-component>
  </div>
</template>

<script>
import CalloutComponent from '@/components/content/Callout.vue';
import HeroComponent from '@/components/content/Hero.vue';
import DfpAdComponent from '@/components/shared/DfpAd.vue';

export default {
  name: 'About',
  components: { CalloutComponent, HeroComponent, DfpAdComponent}
};
</script>

<style lang="scss">
.main {
  padding-top: 5px;
}
.cta {
  margin: 0 5px;
}
.launch-banner {
  
  padding: 3rem 0px !important;
  .container {
    width: 100%;
    max-width: 100%;
    .card {
      border: unset;
      background-color: #dde0e3;
    }
  }
  
}
</style>



